@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
#main {
  width: 21cm;
  height: 29.7cm;
  font-size: 14px;
  margin: 0px auto;
  display: -webkit-flex;
  display: flex;
  line-height: 1.5em;
  box-shadow: 3px 3px 5px 6px #ccc;
  overflow-wrap: break-word;
}

.personalSection {
  width: 37%;
  background-color: #063773;
  color: white;
}

.rightSide {
  width: 63%;
  padding: 20px;
  list-style-position: inside;
}

.remove {
  color: red;
  font-size: 12px;
  font-weight: bold;
  position: absolute;
  top: 0px;
  right: 0px;
  cursor: pointer;
}

.addSection {
  color: green;
  font-size: 14px;
  font-weight: bold;
  margin: 0 auto;
  border: 1px dashed;
  text-align: center;
  padding: 5px;
  margin-bottom: 10px;
}

input {
  width: 200px;
}

input:focus {
  outline: none !important;
  border: 2px dashed var(--myBlue);
}

.textArea {
  width: 100%;
  height: 100%;
}

.textArea:focus {
  outline: none !important;
  border: 2px dashed var(--myBlue);
}

#personalDiv {
  padding: 25px;
}

.pictureDiv {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 20px;
  z-index: 1;
  padding-bottom: 20px;
  object-fit: cover;

}

.image {
  width: 180px;
  max-height: 220px;
  margin: 0 auto;
  display: block;
  z-index: 1;
  object-fit: cover;

}

.imgDiv {
  z-index: 1;
  position: relative;
  max-height: 220px;
  width: 180px;
}

