.personalInfo {
  margin-top: 20px;
  font-weight: bold;
}

.name {
  font-style: "Montserat" !important;
  font-size: 26px;
  font-weight: bold !important;
}

.job {
  font-style: italic;
  font-weight: 900;
  padding: 5px 0px;
}

.contact {
  margin-top: 20px;
  text-align: right;
}

.contactDiv {
  display: -webkit-flex;
  display: flex;
}

.socialMediaProfile {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  line-height: 0px;
  margin-bottom: 2px;
}

.socialMediaProfile i {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 20px;
  height: 20px;
  font-size: 18px;
  color: var(--myBlue);
  padding-left: 5px;
}

.trashSM {
  color: red !important;
}

.skillBar {
  margin-top: 5px;
  width: 200px;
  height: 6px;
  z-index: -1;
}

.skillPercentage {
  background-color: white;
  height: 100%;
  z-index: 10;
  border-radius: 10px;
}

.bars-container{
  margin-top: 20px;
}

.skill {
  margin-top: 10px;
  font-weight: bold;
  font-size: 14px;
}

.tag-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 10px;
  width: 100%;
  -webkit-transform: translateX(-4px);
          transform: translateX(-4px);
}

.tag {
  padding: 2px 6px;
  border-radius: 20px;
  margin-left: 4px;
  margin-top: 5px;
  font-weight: bold;
  font-size: 14px;

}


.eduSection {
  position: relative;
  margin-bottom: 10px;
}

.eduDate {
  display: -webkit-flex !important;
  display: flex !important;
}

.eduHeader {
  display: -webkit-flex;
  display: flex;
  color: var(--myGrey);
  font-weight: bold;
}

.eduGrad {
  font-size: 18px;
  font-weight: bold;
}

.experienceSection {
  padding-bottom: 25px;
  position: relative;
}

.date,
.company {
  color: var(--myGrey);
  font-weight: bold;
}

.company {
  margin-bottom: 5px;
}

.position {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
}

.jobDescription {
  margin-bottom: 5px;
  width: 100%;
  height: px;
}

.list {
  margin-top: 5px;
}

.listItem {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  margin-left: 25px;
}

.listItem::before {
  content: "\26AC";
  color: inherit;
  font-weight: bold;
  display: inline-block;
  width: 1.5em;
  font-size: 25px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  height: 0px;
}

.listItem span {
  padding-top: 4px;
}

.trashItem {
  padding-left: 5px;
}

.addItemDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: var(--myGrey);
  font-size: 14px;
  font-weight: bold;
  padding: 10px;
}

.removeExperience {
  padding: 10px;
}

.trashSection {
  padding-right: 5px;
}

.awardsSection{
    margin-top: 10px;
    position:relative;
}

.awards-Container{
    margin-top: 25px;
}

.addSection{
    margin-top: 20px;
}
.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
  height: 100px;
  background-color: lightgrey;
  margin-bottom: 20px;
  box-shadow: 0px 5px 20px grey;
}

.headingHeader {
  color: white;
}

.buttonDiv button {
  margin: 10px;
  width: 150px;
  height: 50px;
  font-size: 16px;
  border-radius: 10px;
  border: none;
}

.color-view {
  display: -webkit-flex;
  display: flex;
  height: 80%;
}

.color-view__child {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 20px;
  position: relative;
}

.color-view__hex {
  margin-left: 20px;
  border: 2px solid;
  padding: 10px;
}

.color-view__square {
  width: 60px;
  height: 60px;
  border-radius: 10px;
}

.color-view .react-colorful {
  height: 100px;
  width: 100px;
  z-index: 9999999;
  position: absolute;
  top: 60px;
}

:root {
  --main: #063773;
  --myBlue: #69d2ff;
  --myGrey: #777777;
}

#App {
  font-family: "Source Sans Pro";
}

.heading {
  color: #063773;
  border-bottom: 2px solid;
}

#App h2 {
  font-family: "Open Sans" !important;
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: bold;
}

.heading {
  border-bottom: 2px solid;
  text-align: end;
  padding-bottom: 4px;
}

#App h1 {
  font-family: "Open Sans" !important;
  font-size: 36px;
  font-weight: lighter;
}

.printInfo {
  text-align: center;
  padding-bottom: 50px;
}

.editable-hover:hover {
  box-shadow: 0 0 2px 2px;
}

@media print {
  .notPrinted {
    display: none;
  }

  .isPrinted {
    display: block;
    box-shadow: none !important;
  }
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
body {
  line-height: 1;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
nav ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}
del {
  text-decoration: line-through;
}
abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
input,
select {
  vertical-align: middle;
}
html {
  box-sizing: border-box;
}

